const pageView = resolve => require(['@/components/layout/page.vue'],resolve);

const notifyView = resolve => require(['@/pages/module/views/notify.vue'],resolve);
const datePickerView = resolve => require(['@/pages/module/views/datepicker.vue'],resolve);
const popoverView = resolve => require(['@/pages/module/views/popover.vue'],resolve);
const treeView = resolve => require(['@/pages/module/views/tree.vue'],resolve);
const selectView = resolve => require(['@/pages/module/views/mselect.vue'],resolve);
const listView = resolve => require(['@/pages/module/views/list.vue'],resolve);

const router = {
 'path':'/module',
 'name':'module',
 'redirect': '/module/notify',
 'meta':{
  'cn':'组件'
 },
 'component':pageView,
 'children':[{
    'path':'notify',
    'name':'notify',
    'meta':{
     'cn':'消息通知'
    },
    'component':notifyView
  },{
    'path':'datePicker',
    'name':'dataPicker',
    'meta':{
      'cn':'日历组件'
    },
  	'component':datePickerView
  },{
    'path':'select',
    'name':'select',
    'meta':{
      'cn':'下拉选择'
    },
    'component':selectView
  }]
};


export default router;


